export default {
	props: ['storeFlag', 'storeList', 'tag'],
	data() {
		return {
			isCheck: true,
			currentCheck: -1
		}
	},
	methods: {
		closeModal() {
			this.$emit('closeModal');
		},
		checkStore(storeMsg, index, inIndex) {
			if (index != undefined) {
				this.currentCheck = index + '-' + inIndex;
			}
			this.$emit('checkStore', storeMsg)
		}
	}
}
